header {
  margin: 0;
  padding: 50px 20px;
  display: flex;
  background-color: #fff;
  height: 4em;
  align-items: center;
  justify-content: space-between;
  font-weight: bold;
  font-size: 100%;
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
}

.logo {
  display: flex;
  text-align: center;
  justify-content: center;
}


.currency-toggle select {
  padding: 5px 10px; /* Ruang di dalam select */
  font-size: 16px;
  color: gray;
  font-weight: bold; /* Ukuran font */
  border: 1px solid #ccc; /* Border abu-abu */
  border-radius: 4px; /* Sudut yang membulat */
  background-color: transparent; /* Latar belakang putih */
  cursor: pointer; /* Menunjukkan bahwa select bisa diklik */
  transition: border-color 0.3s ease; /* Transisi border saat hover */
  border-color: transparent;
}

.currency-toggle select:hover {
  border-color: transparent; /* Warna border saat hover */
}

.currency-toggle select:focus {
  outline: none;
  border-color: transparent;
}

/* Navigation Menu */
.nav ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

/* Navigation Links */
.nav ul li {
  margin: 0 5px;
  /*Memberikanjarakantaraitemmenu*/
  padding: 6px 10px;
}

.nav ul li a {
  text-decoration: none;
  font-size: 16px;
  color: gray; /* Warna teks */
  transition: color 0.3s ease;
}

/* Button Styles */
.btn {
  background-color: rgb(0, 203, 203);
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-size: 15px;
  font-weight: normal;
}

.btn:last-child {
  background-color: #fff;
  border: 1px solid rgb(0, 203, 203);
}

.btn a {
  color: #fff !important;
  font-weight: 400;
  font-size: 16px !important;
}

.btn:last-child a {
  color: rgb(0, 203, 203) !important; /* Warna teks untuk tombol Sign Up */
}

/* Responsive design */
@media (max-width: 768px) {
  .nav ul {
    flex-direction: column;
    align-items: center;
  }

  .nav ul li {
    margin: 10px 0; /* Jarak vertikal untuk item menu */
  }
}
