/* Container utama untuk slider */
.slider {
  position: relative;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
}

/* Gaya untuk setiap slide */
.slider-content {
  display: flex;
  margin: auto auto auto 5%;
  justify-content: space-between;
  text-align: left;
  color: black;
  position: relative;
  width: 90%;
}

.slider-item h1 {
  padding: 0.5em;
  width: 50%;
  font-size: 220%;
  margin: 0;
  position: absolute;
  bottom: 2em;
}

.slider-item p {
  padding: 1em;
  width: 50%T;
  margin: 0;
  position: absolute;
  bottom: 1em;
  font-size: 200px;
}

.slider-item p {
  font-size: 120%;
  color: #666;
  margin: 8px 0 16px;
}

.slider-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  display: block;
}

/* Tombol panah kiri dan kanan */
.slider-arrow {
  width: 40px;
  height: 40px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 20px;
  color: #333;
  background-color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 50px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1;
  -webkit-border-radius: 50px;
  -moz-border-radius: 50px;
  -ms-border-radius: 50px;
  -o-border-radius: 50px;
  text-align: center;
  line-height: 0px;
}

.slider-arrow.left {
  z-index: 1;
  left: 45px;
}

.slider-arrow.right {
  z-index: 1;
  right: 37px;
}

.slider-arrow:hover {
  background-color: rgba(255, 255, 255, 0.9);
}

/* Titik navigasi di bawah slider */
.slider-dots {
  text-align: center;
  bottom: 10px;
  width: 100%;
}

.dot {
  height: 10px;
  width: 10px;
  margin: 0 4px;
  background-color: #bbb;
  border-radius: 10px;
  display: inline-block;
  cursor: pointer;
  transition: background-color 0.3s;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
}

.dot.active,
.dot:hover {
  background-color: #333;
}

.slide-in {
  animation: slideIn 0.3s forwards;
  -webkit-animation: slideIn 0.3s forwards;
}

@keyframes slideIn {
  from {
    transform: translateX(100%); /* Mulai dari luar kanan */
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
