footer {
  text-align: center;
  align-items: center;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 10px;
  background-color: #f4f4f4;
}

ul {
  display: flex;
  text-decoration: none;
  list-style-type: none;
  justify-content: center;
  font-size: 12px;
}

li {
  margin: 0 10px;
}

a {
  color: rgb(112, 117, 130);
  text-decoration: none;
}

p {
  font-size: 12px;
  color: rgb(185, 185, 185);
}

span,
.bot {
  color: rgb(112, 117, 130);
}
