.cardAlbum {
  display: flex;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  overflow: hidden;
  padding: 8px;
  border-radius: 8px;
  align-items: center;
  border-left: 1px solid #09a809;
  border-bottom: 2px solid #09a809;
  cursor: pointer;
  height: 200px;
}
.imgContainer {
  height: 320px;
  width: 250px;
  border-radius: 4px;
  margin-right: 8px;
  overflow: hidden;
  cursor: pointer;
}
.imgContainer img {
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease;
  -webkit-transition: transform 0.5s ease;
  -moz-transition: transform 0.5s ease;
  -ms-transition: transform 0.5s ease;
  -o-transition: transform 0.5s ease;
}
.imgContainer img:hover {
  transform: scale(1.2);
  -webkit-transform: scale(1.2);
  -moz-transform: scale(1.2);
  -ms-transform: scale(1.2);
  -o-transform: scale(1.2);
}
.imgAlbum {
  width: 100%;
  object-fit: cover;
  height: 100%;
}
.textContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 4px;
  padding: 0;
  cursor: pointer;
}
.title {
  font-size: 14px;
  color: #202020;
  overflow: hidden;
  padding: 0;
  margin: 0;
}
.category {
  font-size: 16px;
  color: #3b3b3b;

}
.description {
  font-size: 12px;
  color: #1c1c1c;
}
.stock {
  font-size: 14px;
  color: #3b3b3b;
  font-weight: normal;
}

.habis {
  background-color: #5e5e5e;
  color: #fff;
  font-weight: bold;
  text-align: center;
  width: 25%;
  font-size: 10px;
  padding: 2px;
  z-index: 1;
  position: inherit;
  top: 0;
}

