.productDetail {
  display: flex;
  padding: 40px;
}

.imageSection {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.imageSection img {
  max-width: 90%;
  height: auto;
}

.detailsSection {
  flex: 1;
  padding: 40px;
}

.detailsSection h1 {
  font-size: 24px;
  margin-bottom: 10px;
  font-weight: normal;
}

.detailsSection h2 {
  color: #3dff84;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 15px;
}

.namaArtis {
  color: #000000;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 0;
  padding: 0;
}

.price {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 15px;
  color: #000000;
  padding-top: 20px;
}

.shippingInfo {
  color: #29a3a7;
  margin-bottom: 30px;

}

.quantityControl {
  display: flex;
  align-items: center;
  margin: 20px 0;
  width: fit-content;
  border: 2px solid #eaeaea;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.quantityControl button {
  width: 30px;
  height: 30px;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}

.quantityControl input {
  width: 50px;
  text-align: center;
  border: none;
  outline: none;
  background-color: transparent;
  border-right: 2px solid #eaeaea;
  border-left: 2px solid #eaeaea;
}

input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.limits {
  display: flex;
  padding: 10px;
  background-color: #f4f4f4;
  color: rgb(79, 79, 79);
  text-align: center;
  line-height: 1;
}

.limits p {
  font-size: 100%;
  color: rgb(79, 79, 79);
}

.total {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
  color: black;
  text-align: right;
}

.actions {
  display: flex;
  gap: 20px;
}

.addToCart,
.buyNow {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}

.addToCart {
  background-color: transparent;
  border: 1px solid rgb(0, 203, 203);
  color: #333;
}

.buyNow {
  background-color: rgb(0, 203, 203);
  color: #fff;
  height: 5em;
}

.deskripsi {
  text-align: center;
  width: 80%;
  position: relative;
  margin: 0 auto;
  padding: 0 0 0 20px;
}

.deskripsi h2 {
  margin-top: 40px;
}

.deskripsi p {
  font-size: 100%;
  color: #000;
  margin-bottom: 40px;
}
